@import "../../../sass/variables.module.scss";

.ProjectDetails {
  width: 50vw;
  @include position;
  
  & .Card {
    & .Content {
      & span {
        @include header;
      }
      & p {
        color: $color-primary;
      }
      & p::first-letter {
        text-transform: uppercase;
      }
    }

    & hr {
      color: $color-text-light;
    }

    & .Details {
      & .Author {
        background: $color-text-light;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        margin-bottom: -2%;
      }
      & .Date {
        background: $color-text-light;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }
  }
}
