@import "../../../sass/variables.module.scss";

.Navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: $color-primary;

  & .Logo {
    color: $color-text;

    & a {
      text-decoration: none;
      color: $color-text;
    }
  }
}

.Links {
  & ul {
    list-style-type: none;

    & li {
      display: inline;

      & a {
        color: $color-text;
        text-align: center;
        padding: 14px 16px;
        text-transform: capitalize;
        text-decoration: none;
        font-weight: 300;


        & .User {
          width: 100px;
          height: 100px;
          border-radius: 50%;
          background-color: $color-pink;
  
        }
      }

      :hover {
        background-color: $color-primary-light;
        border-radius: 5px;
      }

      :focus {
        background-color: $color-primary-light;
        border-radius: 5px;
      }
    }
  }


}
