@import "../../../sass/variables.module.scss";

.Project {
  margin-bottom: 10%;

  & .ProjectSummary {
    & span {
      text-transform: capitalize;
      font-size: 1.5rem;
      color: $color-primary;
    }

    & .Author {
    }
    & .Date {
      margin-top: -5%;

      & p {
        background: $color-text-light;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }
  }
}
