@import "../../sass/variables.module.scss";

.Dashboard {
  & .Row {
    display: flex;
    justify-content: space-around;
    margin-top: 3%;
    & .Projects {
      height: 85vh;
      overflow: auto;

      &::-webkit-scrollbar {
        width: 0.5em;
        border-radius: 3px;
        background-color: wheat;
      }

      &::-webkit-scrollbar-thumb {
        background-color: $color-primary;
        border-radius: 3px;

        &:hover {
          background: $color-primary-light;
        }
      }
    }
    & .Notifications {
    }
  }
}
